import axios from "axios";

function About(props) {
  return (
    <div>
      <h1> {props.title} </h1>
      <p> {props.content}</p>
    </div>
  );
}

About.getInitialProps = async (props) => {
  const slug = split(1, props.asPath).subString(1)
  console.log(slug)
  const res = await axios.get("https://binance-api-example.herokuapp.com/");
  const data = res.data;
  const list = data.filter(val => val.slug === "about");
  return { title: list[0].title, content: list[0].content };
};

export default About;
